.app--header {
    align-items: flex-end;
    margin-right: 5px;
}

.app--header h1 {

}

.app--header__two {
    font-size: 1.2rem;
    color: grey;
}

.row-flex {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 10px;
    padding: .370rem .75rem;
    border-radius: 4px;
    border: 1px solid rgba(0,0,0,.125);
}
.todo-list-item-label
 .todo-list-item {
     font-size: 1.25rem;
 }

.todo-list-item button {
    width: 35px;
    margin: 3px;
}

.todo-list-item-label {
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.todo-list-item.done .todo-list-item-label {
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
    font-weight: bold;
    color: steelblue;
}
.todo-list {
    width: 100%;
}

.todo-list .list-group-item {
    padding: .25rem .75rem;
}


.add-item {
    margin-top: 10px;
}
.todo-app {
    margin: 2rem auto 0 auto;
    max-width: 400px;
}

.top-panel {
    margin: 1rem 0;
}
